<template>
  <div class="ds-question-formwrap">
    <div class="example">
      <el-form label-position="top">
        <el-form-item prop="value" :label="exampleLabel" :rules="exampleRules">
          <el-radio-group v-if="form.layout == '0'">
            <el-radio v-for="el in exampleItems" :key="el.value" :label="el.value">{{ el.text }}</el-radio>
          </el-radio-group>
          <el-radio-group v-else>
            <el-row>
              <el-col v-for="el in exampleItems" :key="el.value" :span="getColSpan">
                <el-radio :label="el.value">{{ el.text }}</el-radio>
              </el-col>
            </el-row>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      class="form"
      label-width="auto"
      label-position="left"
    >
      <el-row :gutter="15">
        <el-col :span="24">
          <el-form-item prop="questionBody" label-width="0">
            <el-input v-model="form.questionBody" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="isOther" label="'':">
            <el-switch v-model="form.isOther"></el-switch>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="readOnly" label=":">
            <el-switch v-model="form.readOnly"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="visible" label=":">
            <el-switch v-model="form.visible"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="required" label=":">
            <el-switch v-model="form.required"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="layout" label=":">
            <el-select v-model="form.layout" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option-group label="">
                <el-option label="2" value="2"></el-option>
                <el-option label="3" value="3"></el-option>
                <el-option label="4" value="4"></el-option>
                <el-option label="5" value="5"></el-option>
              </el-option-group>
            </el-select>            
          </el-form-item>          
        </el-col>
        <el-col :span="12">
          <el-form-item>
            <el-button icon="el-icon-plus" @click="addOption"></el-button>
            <el-button icon="el-icon-circle-plus-outline" @click="batchAddOption"></el-button>            
          </el-form-item>
        </el-col>
        <el-col :span="24" style="padding: 15px 0">
          <el-table :data="form.items">
            <el-table-column
              type="index"
              width="50"
            >
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-form-item size="small" :prop="`items.${scope.$index}.value`" label-width="0" :rules="[{ required: true , message: '' }]">
                  <el-input v-model="form.items[scope.$index].value" placeholder="" @change="(v) => { optionChange(form.items[scope.$index], v) }" />
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              width="180"
              align="right"
            >
              <template slot-scope="scope">
                <el-button v-show="scope.$index === 0" circle size="small" icon="el-icon-circle-plus-outline" @click="addOption"></el-button>
                <el-button circle size="small" icon="el-icon-remove-outline" @click="remove(scope)"></el-button>
                <el-button circle size="small" icon="el-icon-top" :disabled="scope.$index === 0" @click="toggle(scope, 'up')"></el-button>
                <el-button circle size="small" icon="el-icon-bottom" :disabled="scope.$index === form.items.length - 1" @click="toggle(scope, 'down')"></el-button>
              </template>
            </el-table-column>                     
          </el-table>
        </el-col>
        <el-col :span="24">
          <div class="ljsz">
            <span class="label">:</span>
            <span class="btn" @click="showRela"></span>
            <span class="btn" @click="showLogic"></span>
          </div>
        </el-col>
      </el-row>                                
    </el-form>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :modal="true"
      :append-to-body="true"
      :modal-append-to-body="false"
      :before-close="handleClose"
      width="500px"
    >
      <el-input v-model="optionText" type="textarea" :rows="15" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
        <dsf-button type="auxiliary" @click="dialogVisible = false"> </dsf-button>
        <dsf-button @click="sure"> </dsf-button>
      </span>
    </el-dialog>    
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        return {
            form: {
                questionBody: '',
                readOnly: false,
                visible: true,
                required: true,
                isOther: false,
                layout: '0',
                items: [],
            },
            rules: {
                questionBody: [{
                    required: true,
                    message: '',
                }]
            },
            optionText: '',
            dialogVisible: false,
        }
    },

    computed: {
      exampleItems() {
        let arr = this.form.items;
        if (this.form.isOther) {
          arr = [
            ...arr,
            {
              text: '',
              value: '',
            }
          ];
        }
        return arr;
      },
      getColSpan() {
        switch(this.form.layout.toString()) {
          case '1':
            return 24;
          case '2':
            return 12;
          case '3':
            return 8;
          case '4':
            return 6;
          case '5':
            return 4;
          default:
            return 24;
        }
      },
      exampleLabel() {
        return `${this.owner.questionIndex}.${this.form.questionBody}`;
      },
      exampleRules() {
        return this.form.required ? [{ required: true, trigger: 'change' }] : null;
      }
    },

    created(){
      this.owner.layout=this.owner.layout+'';
    },

    methods: {
      optionChange(item, v) {
        item.text = v;
      },
      addOption() {
        const len = this.form.items.length;
        this.form.items.push({
          text: `${len + 1}`,
          value: `${len + 1}`,
          attach_text: ''
        });
      },
      batchAddOption() {
        this.optionText = '';
        this.dialogVisible = true;
      },
      handleClose() {
        this.dialogVisible = false;
      },
      toggle(scope, type) {
        const cindex = scope.$index;
        let nextIndex, arr = [...this.form.items], temp = this.form.items[cindex];
        if ((cindex === this.form.items.length - 1 && type === 'down') || (cindex === 0 && type === 'up')) {
          return;
        }
        if (type === 'up') {
          nextIndex = cindex - 1;
        } else {
          nextIndex = cindex + 1;
        }
        arr.splice(cindex, 1);
        arr.splice(nextIndex, 0, temp);
        this.form.items = arr;
      },
      remove(scope) {
        this.form.items.splice(scope.$index, 1);
      },
      sure() {
        const arr = this.optionText.replace(/^\s|\s$/g, '').split(/\n/).map(it => {
          const str = it.replace(/^\s|\s$/g, '').split('|');
          return {
            text: str[0],
            value: str[1]?str[1]:str[0],
            attach_text: ''
          }
        });
        this.form.items = [...this.form.items, ...arr];
        this.dialogVisible = false;
      },
      showLogic() {
        this.$emit("showDialog",'logic');
      }
    },
}
</script>