<template>
  <el-container class="tree-selector-edit">
    <el-header style="padding: 10px 0px; text-align: right;">
      <DsfButton @click="add"></DsfButton>
    </el-header>
    <el-main style="padding: 0px;">
      <el-table
        ref="table"
        class="ds-table"
        :border="true"
        :data="tableData"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.title"></el-input>
          </template>
        </el-table-column>

        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select
              v-model="scope.row.showCheckBox"
              placeholder="/"
            >
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>

        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template slot="header">
            <el-popover trigger="hover" placement="top">
              <p>:@[isExist(_type,1,2,'3')]</p>
              <div slot="reference" class="name-wrapper">
                <div></div>
              </div>
            </el-popover>
          </template>
          <template v-slot:default="scope">
            <el-input v-model="scope.row.types" placeholder="" />
          </template>
        </el-table-column>

        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template slot="header">
            <el-popover trigger="hover" placement="top">
              <p>
                
              </p>
              <div slot="reference" class="name-wrapper">
                <div></div>
              </div>
            </el-popover>
          </template>
          <template v-slot:default="scope">
            <el-select
              v-model="scope.row.chooseOneNode"
              placeholder="/"
            >
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>

        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.defaultOpenSeries" placeholder="" />
          </template>
        </el-table-column>

        <el-table-column
          v-if="isOuaSelect"
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select v-model="scope.row.metaDataType" placeholder="">
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          v-else
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select v-model="scope.row.metaDataType" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </template>
        </el-table-column>

        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <div v-if="scope.row.metaDataType * 1 > 1">
              <el-select
                v-model="scope.row.showType"
                placeholder=""
                title=""
              >
                <el-option label="" value="user"></el-option>
                <el-option label="" value="dept"></el-option>
              </el-select>

              <el-select
                v-if="
                  scope.row.metaDataType == '4' && scope.row.showType == 'user'
                "
                v-model="scope.row.showSubDept"
                placeholder=""
                title=""
              >
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0"></el-option>
              </el-select>

              <el-input
                v-if="scope.row.metaDataType == '4'"
                v-model="scope.row.showLevel"
                title="0"
              >
                <template slot="prepend"></template>
                <template slot="append"></template>
              </el-input>
            </div>

            <el-input
              v-else-if="scope.row.metaDataType != '1'"
              v-model="scope.row.sqlUrl"
              :disabled="scope.row.metaDataType != '0'"
              placeholder=""
              title=""
            />

            <el-button v-else type="text" @click="setChooseMetaData(scope)">
              
            </el-button>
          </template>
        </el-table-column>

        <!-- <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select v-model="scope.row.async" placeholder="/">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>-->

        <el-table-column
          label=""
          :header-align="'center'"
          :align="'center'"
          :width="120"
        >
          <template v-slot:default="scope">
            <DsfButton
              :size="'small'"
              title=""
              icon="hao"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="insert(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title=""
              icon="guanbi2"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="deleted(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title=""
              icon="shang"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="up(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title=""
              icon="xia"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="down(scope)"
            ></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "TreeSelectorEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      names: [],
      isOuaSelect: false,
    };
  },
  created() {
    if (this.config.params && this.config.params.type == "checkNode") {
      this.isOuaSelect = true;
    }
    this.tableData = this.currentData || [];
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      let res = "";
      if (row.title) {
        this.names[rowIndex] = "";
        let i = this.names.indexOf(row.title);
        if (i != -1 && i < rowIndex) {
          res = "warning-row";
        }
        this.names[rowIndex] = row.title;
      }
      return res;
    },
    newTab() {
      return {
        title: "Tab" + (this.tableData.length + 1),
        showCheckBox: "0",
        types: "",
        chooseOneNode: "1",
        defaultOpenSeries: "1",
        // metaDataType: "1",
        metaDataType: this.isOuaSelect ? "2" : "0",
        showType: "user",
        showSubDept: "1",
        showLevel: "0",
        chooseMetaData: {},
        sqlUrl: "",
        async: "0",
        controls: [],
        name: dsf.uuid(),
        error: false,
      };
    },
    add() {
      // 
      this.tableData.push(this.newTab());
    },
    insert(scope) {
      // 
      this.tableData.splice(scope.$index + 1, 0, this.newTab());
    },
    deleted(scope) {
      dsf.array.remove(this.tableData, scope.row);
    },
    up(scope) {
      if (scope.$index > 0) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex - 1, 0, curr);
      }
    },
    down(scope) {
      if (this.tableData.length - 1 > scope.$index) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex + 1, 0, curr);
      }
    },
    validate() {
      for (let i in this.tableData) {
        this.tableData[i]["title"] = this.tableData[i]["title"] || dsf.uuid();
        let index = this.names.indexOf(this.tableData[i]["title"]);
        if (index != -1 && index < i) {
          dsf.layer.message("", false);
          return false;
        }
      }
      return true;
    },
    yes() {
      return this.tableData;
    },
    setChooseMetaData(scope) {
      const self = this;
      dsf.layer.openDialog({
        title: "",
        fullscreen: false,
        width: "1000px",
        overflow: "hidden",
        content: "TreeDataSource",
        params: {
          chooseMetaData: self['tableData'][scope.$index].chooseMetaData,
        },
        btns: [
          {
            text: "",
            handler: (component) => {
              let status = component.validate();
              if (status) {
                self['tableData'][scope.$index].chooseMetaData = component.yes();
              } else {
                return false;
              }
            },
          },
          {
            text: "",
          },
        ],
      });
    },
  },
};
</script>
