//
import PCBhc from "_platform/dsf/pc/bhc";
//
import mobileBhc from "_platform/dsf/mobile/bhc";
//
import filters from "_platform/dsf/filters";

// 
import question from "_platform/dsf/pc/question";

// 
import exam from "_platform/dsf/pc/exam";

//,
import output from '_platform/dsf/output';
import flow from '_platform/dsf/designer/flow'
//
import designer from "_platform/dsf/designer/page/dsfDesigner";

import '../assets/styles/designer/designer.scss';
import '../assets/styles/pc/onething.scss';

import store from "../store";

import api from '../api';
import { install as components  } from '../pc/components';


//
import App from "./App.vue";
import router from "./router";
import Vue from "vue";
// Vue.use(vant);
Vue.use(PCBhc);
Vue.use(mobileBhc);
Vue.use(filters);
Vue.use(designer);
Vue.use(output);
Vue.use(flow);
Vue.use(question);
Vue.use(exam);
Vue.use(components);

// import { DsfTest } from '../pc/components';
// Vue.component(DsfTest.name, DsfTest);

// //jquery-ui
import( /* webpackChunkName: "jquery-ui" */ "jqueryui/jquery-ui.min.css");
let jqui = import( /* webpackChunkName: "jquery-ui" */ "jqueryui/jquery-ui.min.js");

Vue.prototype.dsf = dsf;
Vue.prototype.$http = dsf.http;
Vue.prototype.$webAPI = api;
var EventBus = new Vue();

//WEBAPI
Object.defineProperties(Vue.prototype, {
  $bus: {
    get: function() {
      return EventBus
    }
  },
  $webAPI: {
    get: function() {
      if (!this.$$webapi) {
        this.$$webapi = {};
        for (let k in api) {
          this.$$webapi[k] = api[k].bind(this)
        }
      }
      return this.$$webapi;
    }
  }
});


Vue.directive(
  "focus", {
    // 
    inserted: function(el) {
      el.focus()
    }
  }
);
router.beforeResolve((to, from, next) => {
  //title
  document.title = to.meta.title || dsf.config.defaultTitle || "";
  next();
})

jqui.then(() => {
  Vue.config.productionTip = false;
  new Vue({
    router,
    store,
    data() {
      return {
        editors: []
      }
    },
    created() {
      //windowresize
      dsf.resizeDetector();
      //IE
      if ("-ms-scroll-limit" in document.documentElement.style && "-ms-ime-align" in document.documentElement.style) {
        //IE
        window.addEventListener("hashchange", () => {
          var currentPath = window.location.hash.slice(1);
          if (this.$router.path !== currentPath) {
            this.$router.replace(currentPath);
          }
        }, false);
      }
    },
    render: h => h(App),
  }).$mount("#app");
});