<template>
  <div class="ds-question-formwrap">
    <div class="example">
      <el-form label-position="top">
        <el-form-item prop="value" :rules="exampleRules">
          <template #label>
            <span v-html="example"></span>
          </template>
        </el-form-item>
      </el-form>
    </div>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      class="form"
      label-width="auto"
      label-position="left"
    >
      <el-row :gutter="15">
        <el-col :span="24">
          <el-form-item prop="questionBody" label-width="0">
            <el-input v-model="form.questionBody" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="alert">
          <div>: ()</div>
          <el-button @click="addCode"></el-button>      
        </el-col>
        <el-col :span="12">
          <el-form-item prop="isNumber" label=":">
            <el-switch v-model="form.isNumber"></el-switch>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="readOnly" label=":">
            <el-switch v-model="form.readOnly"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="visible" label=":">
            <el-switch v-model="form.visible"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="required" label=":">
            <el-switch v-model="form.required"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="24">
          <div class="ljsz">
            <span class="label">:</span>
            <span class="btn" @click="showRela"></span>
          </div>
        </el-col>
      </el-row>                                
    </el-form>
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        return {
            form: {
                questionBody: '',
                isNumber: false,
                readOnly: false,
                visible: true,
                required: true,
            },
            rules: {
                questionBody: [{
                    required: true,
                    message: '',
                }]
            }
        }
    },
    computed: {
      example() {
        const slot = this.form.questionBody.replace(/\(\)|\\/g, `
          <input type="text" placeholder="" class="ds-qi-fill">
        `);        
        return `${this.owner.questionIndex}.${slot}`;
      },
      exampleRules() {
        return this.form.required ? [{ required: true, trigger: 'change' }] : null;
      }
    },
    methods: {
      addCode() {
        this.form.questionBody += '()';
      }
    },
}
</script>